      handleclick(e){

         var sumall = this.state.arr[0] + this.state.arr[1] + this.state.arr[2] + this.state.arr[3] + this.state.arr[4] + this.state.arr[5] + this.state.arr[6] + this.state.arr[7] + this.state.arr[8]+this.state.arr[9]; 
         var sum10 = this.state.arr[9];
        if(sumall <10 && sum10<2){
            this.setState({education:true})
        }else if(sumall <10 && sum10 >=2){
            this.setState({appointment:true})
        }else if(sumall>10 && sum10<2){
            this.setState({appointment:true})
        }else if(sumall>10 && sum10>=2){
            this.setState({appointment:true})
        }



          this.setState({result:true})
      }


   
